@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Layout{width:100%;height:100vh;background-color:#020F38;font-family:'Open Sans', sans-serif;padding:15px;font-weight:600;box-sizing:border-box}.Layout .Title{color:#fff;margin-bottom:10px}.Layout .LayoutMost{display:flex;flex-wrap:wrap;flex-direction:row;color:#020F38}.Layout .LayoutMost>div{height:100px;border-radius:15px;margin-bottom:12px;box-sizing:border-box;padding:15px;overflow:hidden;position:relative}.Layout .LayoutMost>div .title{margin-bottom:10px}.Layout .LayoutMost>div .time{font-size:13px;font-weight:600}.Layout .LayoutMost>div:first-child{flex-basis:100%}.Layout .LayoutMost>div:nth-child(2){flex-basis:37%}.Layout .LayoutMost>div:last-child{flex-basis:60%;margin-left:auto}.Layout .LayoutMost .pink{background-color:#FFEAEC}.Layout .LayoutMost .blue{background-color:#82ADFF}.Layout .LayoutMost .green{background-color:#97D9B4}.Layout .LayoutMost .circle:after{content:'';width:120px;height:120px;border-radius:100%;background:-webkit-gradient(linear, left bottom, left top, from(#f4aed0), to(#FFB9DB));background:linear-gradient(to top, #f4aed0, #FFB9DB);position:absolute;right:-30px;top:30px}.Layout .LayoutMost .square:after{content:'';width:90px;height:90px;-webkit-transform:rotate(45deg);transform:rotate(45deg);background:-webkit-gradient(linear, left bottom, left top, from(#6e48d9), to(#8f70e7));background:linear-gradient(to top, #6e48d9, #8f70e7);position:absolute;right:-30px;top:40px}

